@import url('https://fonts.googleapis.com/css2?family=Alata&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Abel&family=Alata&display=swap');
.header {
    display: flex;
    align-items: center;
    color: white;
    background-color:black;
    top: 0;
    position: sticky;
    z-index: 2000;
}
.header_left{
    flex: 0.4;
    font-family: "abel",serif;
    
}
.header .header_left h1 {
    text-align: center;
    font-family: "abel",serif;
    letter-spacing: 2px;
    padding: 20px 0;
}
.header_left h1 span {
    color: #ad33fb;
    
}
.header_right {
    display: flex;
    flex: 0.6;
    justify-content: space-evenly;
    align-items: center;
}
.header_right  h4 {
    font-size: 18px;
    cursor: pointer;
    transition: transform 200ms ease-in-out;
    font-family: "abel",serif;
    padding: 20px 0;
    letter-spacing: 1px;
}
.header_right h4:hover {
    transform: scale(1.09);
    background-color: #ad33fb;
    color: white;
    padding: 5px 20px;
    border-radius: 1px;
    letter-spacing: 1px;
    font-family: "abel",serif;
    font-size: 18px;
    cursor: pointer;
}
.header_rightbutton {
    background-color: #ad33fb;
    color: white;
    padding: 5px 20px;
    letter-spacing: 1px;
    cursor: pointer;
}

.select_container{
    background-color: #ad33fb;
    color: white;
    font-family: "abel",serif;
    font-weight: 800;
    cursor: pointer;
    font-size: medium;
    
}