@import url('https://fonts.googleapis.com/css2?family=Abel&family=Alata&family=Rubik:ital,wght@0,300;0,400;1,300&display=swap');

.Projectcontainer{
          
          background-color: black;
          align-items: center;
          margin: 0;
          
}
.container_content{
    color: aliceblue;
    font-family: " montserrat", sans-serif;
    font-weight: 600;
    font-size: xx-large;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: underline;
    text-transform: uppercase;
    text-decoration-line: inherit;  
}
.container_image1 img {
    width: 350px;
    margin: 5% 5%;
} 
 .content_wrapper img {
    max-width: 100%;
} 
.container_image1 {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.container_image1 .content_wrapper {
    position: relative;
    width: 23%;
    margin: 5%;
}
.container_image1 .content_wrapper img {
    max-width: 100%;
}
.container_image1 .content_wrapper .content1 h1 p{
    padding-bottom: 15px;
}
.container_image2 img {
    width: 350px;
    margin-left: 15%;
    padding-left: 2.5%;
    padding-right: 2.5%;
    cursor: pointer;
}
.container_image3 img {
    width: 350px;
    margin: 5% 5%;
}
.container_image4 img {
    width: 350px;
    margin-left: 15%;
    padding-left: 2.5%;
    padding-right: 2.5%;
}
.container_image5 img {
    width: 350px;
    margin: 5% 5%;
}
.content1{
    position: absolute;
    transition: 0.6s;
    opacity: 0;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    text-align: center;
}
.tag h3{
    color: #c97ef8;
    font-weight: 600;
    font-size: 50px;
    padding: 30% 0 ;
   
}
.tag_1 h3 {
    color: #ad33fb;
    font-weight: 600;
    font-size: 50px;
    padding: 30% 0;
    
}
.content_wrapper:hover img {
    opacity: 0.3;
}
.content_wrapper:hover .content1{
    opacity: 1;
} 
.content1 h1 {
    font-size: 30px; 
    font-family: "montserrat",sans-serif;
    margin-bottom: 15px;
    color:rgb(245, 245, 245);
    margin-left: 5px;
   
}
.content1 p {
    font-size: 30px; 
    font-family: "montserrat",sans-serif;
    font-size: 20px;
    color: white;
    margin-left: 9px;
}
.content1 *{
    transform: translateY(0px);
    transition: transform 0.6s;
}

