@import url('https://fonts.googleapis.com/css2?family=Abel&family=Alata&display=swap');
.contact {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-image: url(../../../Public/images/Black-background.jpg);     
    padding-bottom: 5%;
}
.contact h1{
    color: white;
    margin-top: 5%;
}
.contact_container{
    width: 600px;
    height: 200px;
    border: solid white 2px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 30px;
    margin-top: 20px;
    margin-bottom: 5%;
    font-family: "alata", sans-serif;
    font-size: 20px;
    font-weight: 400;
    
}
.contact_container p {
    color: white;
    text-align: left;
    font-size: 20px;
}
.contact_container p span {
    color: #ad33fb;
    font-family: "alata", sans-serif;
    font-size: 20px;
    font-weight: 400;
}