}
img {
  max-width: 120%;
}
.skillcontainer_image{
  flex: 0.4;
  margin-top: 10%;
  margin-bottom: 10%;
  margin-left: 18%;
  margin-right: 4%;
  
 
 
}
.skillcontainer_image img {
  width: 700px;
  object-fit: contain;
  border-radius: 5px;
}
.skillcontainer_text {
    color: aliceblue;
    flex: 0.0; 
    text-align: left;
    width: 100%;
   
}
h5 {
  margin: 0;
}
.icon-wrapper {
    position: relative;
    padding-right: 15px;
    background-color: black;
    width: 100%;
    margin-left: 50%;
    
  }
  .skillcontainer_slider{
    width: 100%;
  }
  
  
  :where(.css-dev-only-do-not-override-15rg2km).ant-slider .ant-slider-handle::after,
  :where(.css-15rg2km).ant-slider .ant-slider-handle::after {
    position: absolute;
    inset-block-start: 0;
    inset-inline-start: 0;
    width: 10px;
    height: 10px;
    background-color: #d209ba;
    box-shadow: 0 0 0 2px #000000f7;
    border-radius: 50%;
    cursor: pointer;
    transition: inset-inline-start 0.2s,inset-block-start 0.2s,width 0.2s,height 0.2s,box-shadow 0.2s;
}

